const textSets = {top100: _.shuffle(_.values(top100)), musicals01: _.shuffle(_.values(musicals01))};

export const getTextFunction = function* (nameOfSet) {
    const textArray = textSets[nameOfSet];
    let index = -1;
    while(true) {
        index = index < textArray.length - 1 ? index+1 : 0;
        const text = textArray[index];
        yield text;
    }
}



